export function omitProps(props, propsToOmit) {
    return Object.entries(props)
        .filter(([propKey]) => !propsToOmit.includes(propKey))
        .reduce((filteredProps, [propKey, propValue]) => (Object.assign(Object.assign({}, filteredProps), { [propKey]: propValue })), {});
}
export function getComponentTheme(theme, componentName) {
    return theme.components[componentName];
}
